export default {
	data() {
		return {
			cardCur: 0,
			camlists: [
				{
					camid: '0000',
					imgurl: 'https://filepr-1259018500.cos.ap-beijing.myqcloud.com/nmvapp/pics/fight.jpg',
					time: '20-01-01-23:59:59'
				},
				{
					camid: '0001',
					imgurl: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1585941578726&di=869a3ab0a1b528f64047c8cc93d7071e&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20180824%2Fe85dd3ddddd941e3a380b3b8b7ebe40b.gif',
					time: '20-01-01-23:59:59'
				},
				{
					camid: '0010',
					imgurl: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1585941754959&di=02c92e5633f776576710e796401f51a6&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20180831%2Fb1f1bb4687504f1a891bb67652ebf995.gif',
					time: '20-01-01-23:59:59'
				},
				{
					camid: '0011',
					imgurl: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1585942446075&di=53c84058ee3a728f417b3fbb4c37acd3&imgtype=0&src=http%3A%2F%2Fwww.17qq.com%2Fimg_biaoqing%2F64030871.jpeg',
					time: '20-01-01-23:59:59'
				},
				{
					camid: '0100',
					imgurl: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1585942209451&di=8fe7da9b3b3b467b405c701722b63208&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fq_mini%2Cc_zoom%2Cw_640%2Fimages%2F20170729%2F2f909fcc57ba445da7042c22e5adbe03.gif',
					time: '20-01-01-23:59:59'
				},
				{
					camid: '0101',
					imgurl: 'https://filepr-1259018500.cos.ap-beijing.myqcloud.com/nmvapp/pics/fight.gif',
					time: '20-01-01-23:59:59'
				},
			],
			dotStyle: false,
			towerStart: 0,
			direction: ''
		}
	},
	onLoad() {
		this.TowerSwiper('camlists');
		// this.TowerSwiper('flamesmokelists');
		// 初始化towerSwiper 传已有的数组名即可
	},
	methods: {
		tolivecams(e){
			uni.redirectTo({
				url:'livecams/0000'
			})
		},
		DotStyle(e) {
			this.dotStyle = e.detail.value
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		// towerSwiper
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
				list[i].mLeft = i - parseInt(list.length / 2)
			}
			this.swiperList = list
		},
		
		// towerSwiper触摸开始
		TowerStart(e) {
			this.towerStart = e.touches[0].pageX
		},
		
		// towerSwiper计算方向
		TowerMove(e) {
			this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
		},
		
		// towerSwiper计算滚动
		TowerEnd(e) {
			let direction = this.direction;
			let list = this.swiperList;
			if (direction == 'right') {
				let mLeft = list[0].mLeft;
				let zIndex = list[0].zIndex;
				for (let i = 1; i < this.swiperList.length; i++) {
					this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
					this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
				}
				this.swiperList[list.length - 1].mLeft = mLeft;
				this.swiperList[list.length - 1].zIndex = zIndex;
			} else {
				let mLeft = list[list.length - 1].mLeft;
				let zIndex = list[list.length - 1].zIndex;
				for (let i = this.swiperList.length - 1; i > 0; i--) {
					this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
					this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
				}
				this.swiperList[0].mLeft = mLeft;
				this.swiperList[0].zIndex = zIndex;
			}
			this.direction = ""
			this.swiperList = this.swiperList
		},
	}
}
